import { Injectable } from '@angular/core';
import {Resolve} from "@angular/router";
import {HttpClient} from "@angular/common/http";
import {ISimpleResponse} from "./i-simple-response";
import {pluck} from "rxjs/internal/operators";
import {Observable, of} from "rxjs/index";
import {environment} from "../environments/environment";

@Injectable({
  providedIn: 'root'
})
export class AuthResolveService implements Resolve<boolean> {
  resolve(): Observable<boolean> {
    const token = localStorage.getItem('AuthToken');

    if (token) {
      return this.httpClient.post<ISimpleResponse>(environment.api + 'uservalid', {token}).pipe(pluck<ISimpleResponse, boolean>('status'));
    }

    return of(false);
  }

  constructor(private httpClient: HttpClient) { }
}
