import {Component} from '@angular/core';
import {INavigationLink} from "./_index/index/index.component";
import {NavigationEnd, Router, RouterEvent} from "@angular/router";
import {filter, map} from "rxjs/internal/operators";
import {Observable} from "rxjs/index";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  public nav: Array<INavigationLink> = [
    {name: 'About', href: 'about'},
    {name: 'Articles', href: 'articles'},
    {name: 'Diplomas', href: 'diplomas'},
    {name: 'Services', href: 'services'},
    {name: 'Reviews', href: 'reviews'},
    {name: 'Contacts', href: 'contacts'},
  ];
  public navVisible: Observable<NavigationEnd>;

  constructor(private router: Router) {
  }

  ngOnInit() {
    this.navVisible = this.router.events.pipe(
      filter<RouterEvent>((e: RouterEvent) => e instanceof NavigationEnd),
      map((e: NavigationEnd) => e)
    );
  }
}
