import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {AboutComponent} from './about/about.component';
import {IndexComponent} from './_index/index/index.component';
import {ArticleComponent} from './article/article.component';
import {ArticlesComponent} from './articles/articles.component';
import {DiplomaComponent} from './diploma/diploma.component';
import {DiplomasComponent} from './diplomas/diplomas.component';
import {ServiceComponent} from './service/service.component';
import {ServicesComponent} from './services/services.component';
import {ReviewComponent} from './review/review.component';
import {ReviewsComponent} from './reviews/reviews.component';
import {AuthService} from './auth.service';
import {AuthResolveService} from './auth-resolve.service';
import {LoginComponent} from './login/login.component';
import {ContactsComponent} from './contacts/contacts.component';
import {PageDataResolverService} from './page-data-resolver.service';
import {IPage} from './ipage';

export function sortByLanguage(array: Array<IPage>) {
  const result = {};

  array.forEach((item: IPage) => {
    result[item.language] = item;
  });

  return result;
}

const routes: Routes = [
  {
    path: '',
    component: IndexComponent,
    canActivate: [AuthService],
    canLoad: [AuthService]
  }, {
    path: 'login',
    component: LoginComponent,
    resolve: {loginSuccess: AuthResolveService}
  }, {
    path: 'about',
    component: AboutComponent,
    canActivate: [AuthService],
    canLoad: [AuthService],
    data: {resolverData: 'about', resolverFn: sortByLanguage},
    resolve: {pageData: PageDataResolverService}
  }, {
    path: 'article/:id',
    component: ArticleComponent,
    canActivate: [AuthService],
    canLoad: [AuthService]
  }, {
    path: 'articles',
    component: ArticlesComponent,
    canActivate: [AuthService],
    canLoad: [AuthService],
    data: {resolverData: 'article', resolverFn: sortByLanguage},
    resolve: {pageData: PageDataResolverService}
  }, {
    path: 'diploma/:id',
    component: DiplomaComponent,
    canActivate: [AuthService],
    canLoad: [AuthService]
  }, {
    path: 'diplomas',
    component: DiplomasComponent,
    canActivate: [AuthService],
    canLoad: [AuthService],
    data: {resolverData: 'diploma', resolverFn: sortByLanguage},
    resolve: {pageData: PageDataResolverService}
  }, {
    path: 'service/:id',
    component: ServiceComponent,
    canActivate: [AuthService],
    canLoad: [AuthService]
  }, {
    path: 'services',
    component: ServicesComponent,
    canActivate: [AuthService],
    canLoad: [AuthService],
    data: {resolverData: 'service', resolverFn: sortByLanguage},
    resolve: {pageData: PageDataResolverService}
  }, {
    path: 'review/:id',
    component: ReviewComponent,
    canActivate: [AuthService],
    canLoad: [AuthService]
  }, {
    path: 'reviews',
    component: ReviewsComponent,
    canActivate: [AuthService],
    canLoad: [AuthService],
    data: {resolverData: 'review', resolverFn: sortByLanguage},
    resolve: {pageData: PageDataResolverService}
  }, {
    path: 'contacts',
    component: ContactsComponent,
    canActivate: [AuthService],
    canLoad: [AuthService],
    data: {resolverData: 'contacts', resolverFn: sortByLanguage},
    resolve: {pageData: PageDataResolverService}
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: []
})
export class AppRoutingModule {
}
