import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {IPageLanguageContainer} from '../ipage-language-container';
import {IPageSubmit} from "../ipage-submit";
import {NgForm} from "@angular/forms";
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {environment} from "../../environments/environment";
import {FileChangeEvent} from "@angular/compiler-cli/src/perform_watch";
import {map} from "rxjs/internal/operators";

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit {
  public langObject: IPageLanguageContainer;
  public lang: string = 'en';
  private requestObj: IPageSubmit = {
    id: 'about',
    page: {}
  };
  private filesToUpload: Array<File> = [];

  handleFileInput(fileInput: any) {
    this.filesToUpload = <Array<File>>fileInput.target.files;
  }

  constructor(private route: ActivatedRoute, private httpClient: HttpClient) {
  }

  submit(e: NgForm) {
    for (let i in e.controls) {
      if (e.controls[i].dirty) {
        this.requestObj.page[i] = e.controls[i].value;
      }
    }

    const formData: any = new FormData();
    const files: Array<File> = this.filesToUpload;
    console.log(files);

    for (let i = 0; i < files.length; i++) {
      formData.append("uploads[]", files[i], files[i]['name']);
    }
    console.log('form data variable :   ' + formData.toString());
    this.httpClient.post(environment.api + 'upload', formData)
      .pipe(
        map((files: any) => files.json())
      )
      .subscribe(files => console.log('files', files));
  }

  ngOnInit() {
    this.langObject = this.route.snapshot.data.pageData;
  }
}
