import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve} from '@angular/router';
import {Observable} from 'rxjs/index';
import {HttpClient} from '@angular/common/http';
import {environment} from '../environments/environment';
import {map} from 'rxjs/internal/operators';
import {IPage} from './ipage';

@Injectable({
  providedIn: 'root'
})
export class PageDataResolverService implements Resolve<IPage> {
  resolve(route: ActivatedRouteSnapshot): Observable<IPage> {
    return this.httpClient.get<IPage>(environment.api + '/interface', {params: {lang: ['en', 'ru', 'uk'], id: route.data.resolverData}})
      .pipe(map((pages: Array<IPage>) => route.data.resolverFn(pages)));
  }

  constructor(private httpClient: HttpClient) {
  }
}
